// axioshttp
import axios, { AxiosResponse } from "axios";
// token|
import router from "../router";
// 
import {
  HTTP_STATUS_MSG_404,
  HTTP_STATUS_MSG_401,
  HTTP_STATUS_MSG_5XX,
  HTTP_STATUS_TITLE_ERROR,
  HTTP_STATUS_TITLE_5XX
} from "../constants/TEXT";
// Content-Type:application/x-www-form-urlencoded jsonJSON.stringify()
import qs from "qs";
// element-ui
import { Notification, Loading } from "element-ui";
import { PATH_LOGIN } from "@/constants/URL";

// axios
const axiosCreate = {
  // apiurl
  baseURL: process.env.VUE_APP_API_URL,
  // 
  timeout: 30000,
  // cookie
  withCredentials: true,
  validateStatus: function(status: number) {
    // 500Reject 5XXcatch
    return status < 400;
  }
};

/**
 * postContent-Type
 * application/x-www-form-urlencodedapplication/json
 * application/x-www-form-urlencoded
 * application/jsonqs
 */
const postHeaders = "application/x-www-form-urlencoded";

// axios
const http = axios.create(axiosCreate);
let loadingInstance: any;

/**
 * axios request
 * 
 * {
 *  data:{    
 *   errcode:'1'/'2'/'3'...    errcode
 *  },
 *  errmsg:''       http444errmsg
 * }
 */
http.interceptors.request.use(
  config => {
    // loadingInstance = Loading.service({ fullscreen: true });
    config.headers["Authorization"] =
      "Bearer " + localStorage.getItem("talToken");
    if (config.method === "post" || config.method === "put") {
      config.data = qs.stringify(config.data);
      // postputContent-Type:application/json
      config.headers["Content-Type"] = postHeaders;
    }
    return config;
  },
  error => {
    // loadingInstance.close();
    Promise.reject(error);
  }
);

/**
 * axios respone
 * ts
 * element-ui
 */
http.interceptors.response.use(
  config => {
    return config.data || {};
  },
  (error: any) => {
    // loadingInstance.close();
    // ||token401
    if (error.response.status === 401) {
      Notification({
        type: "error",
        title: HTTP_STATUS_TITLE_ERROR,
        message: error.response.data.message || HTTP_STATUS_MSG_401,
        duration: 3000
      });
      router.push({ path: PATH_LOGIN });
    } else if (error.response.status >= 400 && error.response.status < 500) {
      Notification({
        type: "error",
        title: HTTP_STATUS_TITLE_ERROR,
        message: error.response.data.message || HTTP_STATUS_TITLE_ERROR,
        duration: 3000
      });
    } else {
      Notification({
        // axiosCreatevalidateStatus>=500  () 
        type: "error",
        title: HTTP_STATUS_TITLE_5XX,
        message: HTTP_STATUS_MSG_5XX,
        duration: 3000
      });
    }

    return Promise.reject(error.response);
  }
);
